.emailRow {
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid whitesmoke;
    cursor: ponter;
    z-index: 999;
}

.emailRow:hover {
    border-top: 1px solid whitesmoke;
    box-shadow: 0px 4px 4px -2px rgba(0, 0, 0, 0.24);
}

.emailRow--options {
    display: flex;
}

.emailRow--message {
    display: flex;
    flex: 0.8;
    align-items: center;
    font-size: 13px;

}

.emailRow--description {
    font-weight: 400;
    color: gray;
}

.emailRow--title {
    font-size: 13px;
    flex: 0.3;
}

.emailRow--message h4 {
    width: 400px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 5px;

}

.emailRow--time {
    padding-right: 15px;
    font-size: 9px;
    font-weight: bold;
}